<template>
    <div class="main">
        <div class="box">
          <!--   <header>
                <span class='el-icon-arrow-left' @click='jump'></span>
                {{name}}
            </header> -->
            <router-view></router-view>
            <!--<keep-alive>
            </keep-alive>-->
        </div>

    </div>
</template>

<script>
    //import  from ''

    export default {
        //组件名
        name: 'index',
        //实例的数据对象
        data() {
            return {
                name:"",
            }
        },
        //数组或对象，用于接收来自父组件的数据
        props: {

        },
        //计算
        computed: {

        },
        //方法
        methods: {
            jump(){
                this.$router.go(-1);
            }
        },
        //生命周期函数
        created() {
            console.log(this.$route.name);
            this.name = this.$route.name;
        },
        beforeMount() {

        },
        mounted() {

        },
        //监视
        watch: {
            // console.log(this.$route.name);
            '$route':function(){
                // console.log(this.$route.name);
                if(this.$route.name){
                    this.name = this.$route.name;
                }
                // if(this.$route.name=='注册用户审核'){
                //   this.waitAudit();
                //   this.alreadAudit();
                // };
            }
        },
        //组件
        components: {

        },
        //过滤器
        filters:{

        },
        //自定义指令
        directive:{

        }
    }
</script>

<!--
	作者：liangyanxiangde@163.com
	时间：2017-03-27
	描述：统一使用less,局部样式
-->
<style lang="less" scoped>
@import "../../less/modules/base.less";
.box{
    max-width: 10rem;
    margin: 0 auto;
    margin-bottom:1rem;
}
header{
    height:.90rem;
    line-height: .90rem;
    padding:0 .30rem;
    text-align: center;
    font-size:.30rem;
    color:#000;
    border-bottom:.01rem solid #ccc;
    .el-icon-arrow-left{
        font-size:.28rem;
        float:left;
        line-height: .90rem;
    }
}

</style>


